.container{
    width: 100%;
    height: 65vh;
    background-color: aquamarine;
    transition: height 2s ease-in-out;
}

.containeOfMobile{
    width: 100%;
    height: 100vh;
    background-color: aquamarine;
    transition: height 2s ease-in-out, translateY 2s ease ease-in-out;
}


.thisOne2{
    width: 100%;
    height: 100%;
    background-color: rgb(134, 170, 158);
    transition: height 2s ease-in-out;
    display: none;
    overflow: scroll;
}

.active{
    display:block;
}


.Button__Container{
    position: relative;
    bottom: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.swipe{
    position: relative;
    z-index: 15;
    scale: 300%;
    color: aliceblue;
    margin-bottom: 0.5vw;
}